import React, { useEffect } from "react";
import { Grid, Box, Typography } from "@material-ui/core";
import Card from "@material-ui/core/Card";
import { makeStyles } from "@material-ui/core/styles";
import NumberFormat from 'react-number-format';
import { initOnLoad } from "apexcharts";


const useStyles = makeStyles((theme) => ({
  typography: {
    fontSize: "2rem",
    color:"#0d47a1",
    display:"inline",
    fontWeight:"500",
     [theme.breakpoints.down("sm")]: {
      fontSize: "1.1rem",
    },
  },
  head:{
      fontSize: "2rem",
      [theme.breakpoints.down("sm")]:{
          fontSize: "1rem",
      }
  },
}));



const PieCard = ({ productive }) => {
    let preProductiveFormat = new Intl.NumberFormat('en-ca', {style: 'decimal'}).format(productive.preReProductive);
    let reProductiveFormat = new Intl.NumberFormat('en-ca', {style: 'decimal'}).format(productive.reProductive);
    let postProductiveFormat = new Intl.NumberFormat('en-ca', {style: 'decimal'}).format(productive.postProductive);
    let totalPopulation = new Intl.NumberFormat('en-ca', {style: 'decimal'}).format(productive.Total);
    let totalUndefine = new Intl.NumberFormat('en-ca', {style: 'decimal'}).format(productive.unDefine);
    
  const classes = useStyles();
  return (
    <React.Fragment>
    
      <Box pb={3}>
      <Card>
          <Box py={4} px={7} align="center">
          <Typography  color="secondary" variant="h5" display="inline" className={classes.head}>
            Total population of
            <Typography
              varaint="h1"
              color="primary"
              align="center"
              className={classes.typography}
            >
              {` ${productive.Name} `}
            </Typography>
            is <Typography  className={classes.typography}>{` ${totalPopulation} `}</Typography> 
            and have people are not registered  <Typography  className={classes.typography}>{` ${totalUndefine} `}</Typography> 
            and it has the <Typography  className={classes.typography}>{` ${Number(productive.Rank).toFixed(0)}th `}</Typography>
            largest population in the {(productive.Name === 'Thailand')? (` world `): (` country `)} with the population of the 
            <Typography   className={classes.typography}>{` pre-reproductive `}</Typography> 
            at  <Typography   className={classes.typography}>{` ${preProductiveFormat} `}</Typography> 
            and  <Typography   className={classes.typography}>{` reproductive `}</Typography> 
            at <Typography  className={classes.typography}>{` ${reProductiveFormat} `}</Typography> and 
             <Typography className={classes.typography}>{` post-reproductive `}</Typography> 
            at  <Typography  className={classes.typography}>{` ${postProductiveFormat} `}</Typography> 
          </Typography>
        </Box>
      </Card>
     </Box>
    </React.Fragment>
  );
};

export default PieCard;
